import * as React from 'react';
import { withStyles } from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

interface MyProps {
    title: string,
    description: string,
    publishDate: string,
    classes: {
        card: string,
        bullet: string,
        title: string,
        pos: string
    }
}

interface MyState {}

const styles : any = {
    card: {
        backgroundColor: 'rgba(255, 255, 255, 0.7)',
        margin: '10px 0',
        border: '1px solid #000',
        transition: '0.4s',
        backgroundImage: 'url(data:image/png;base64,)',
        '&:hover': {
            boxShadow: '5px 5px 5px 5px rgba(0,0,0,0.5)'
        }
    },
    bullet: {
        display: 'inline-block',
        margin: '0 2px',
        transform: 'scale(0.8)',
    },
    title: {
        fontSize: 14,
    },
    pos: {
        marginBottom: 12,
    },    
}

class MyCard extends React.PureComponent<MyProps, MyState> {
    constructor(props : MyProps) {
        super(props);
        this.state = {
            open: [],
            progress: [],
            published: []
        };
    }

    render() {
        const { classes, title, description, publishDate } = this.props;
        return (
            <Card className={classes.card}>
                <CardContent>
                    <Typography className={classes.title} color="textSecondary" gutterBottom>
                        { publishDate }
                    </Typography>
                    <Typography variant="h5" component="h2">
                        { title }
                    </Typography>
                    <Typography component="p">
                        { description }
                    </Typography>
                </CardContent>
                <CardActions>
                    <Button variant="contained" color="primary" size="small">Edit</Button>
                    <Button variant="contained" color="secondary" size="small">Delete</Button>
                </CardActions>                
            </Card>
        )
    }
}


export default withStyles(styles)(MyCard);
